@charset "UTF-8";

@font-face {
  font-family: "shore-icons";
  src:url("fonts/shore-icons.eot");
  src:url("fonts/shore-icons.eot?#iefix") format("embedded-opentype"),
    url("fonts/shore-icons.woff") format("woff"),
    url("fonts/shore-icons.ttf") format("truetype"),
    url("fonts/shore-icons.svg#shore-icons") format("svg");
  font-weight: normal;
  font-style: normal;

}

[data-icon]:before {
  font-family: "shore-icons" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="shore-icon-"]:before,
[class*=" shore-icon-"]:before {
  font-family: "shore-icons" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

